<!-- 首页滚动通知 -->
<template>
    <div class="home-notice">
        <div class="notice-icon">
            <img :src="imgs.notice" alt="notice">
        </div>
        <div class="notice-content">
            <span class="notice-text" :style="{animationDuration: notice.length/5+'s'}">{{notice}}</span>
        </div>
        <div class="notice-more">
            <div class="notice-btn">更多</div>
        </div>
    </div>
</template>

<script>
import NoticeImg from '../assets/notice.png'

export default {
    data () {
        return {
            imgs: {
                notice: NoticeImg
            },
            notice: `1．建议使用个人虚拟币钱包，非交易所钱包。
2．建议一次多买些虚拟币并在放置几小时后提至个人虚拟币钱包存放，以便之后一段时间里可直存入平台收款账户，提款和售出都建议您分次操作，可分2或3次完成提币。
3．若非着急使用人民币，可将提款的虚拟币存放于个人虚拟币钱包，视情况再售出，免去重复买入再提币的操作。
4．建议在交易所进行币币交易（用USDT换成其它种类虚拟币比如BTC，也可再换回来），少量兑换待价格适宜再售出，虚拟币投资行为有利于账号的长期使用。
5．非币圈用户，不建议使用火币交易所进行买币提币操作。`
        }
    }
}
</script>

<style scoped>
.home-notice {
    display: flex;
    align-items: center;
    width: 1200px;
    height: 40px;
    padding: 0 10px 0 18px;
    margin: 15px auto 0 auto;
    border-radius: 20px;
    background-image: -webkit-gradient(linear,left top, left bottom,color-stop(14%, #ffffff),color-stop(74%, #ecefff),color-stop(98%, #fefeff));
    background-image: -webkit-linear-gradient(top,#ffffff 14%,#ecefff 74%,#fefeff 98%);
    background-image: linear-gradient(to bottom,#ffffff 14%,#ecefff 74%,#fefeff 98%);
    border: solid 1px #d1d5f5;
}
.notice-content {
    flex: 1;
    overflow: hidden;
}
.notice-text {
    white-space: nowrap;
    color: #9da5bb;
    cursor: pointer;
    display: inline-block;
    animation: run 10s linear infinite;
}
.notice-content:hover {
    cursor: pointer;
}
.notice-content:hover .notice-text {
    animation-play-state: paused;
}
@keyframes run {
    0% { transform: translateX(1030px) }
    100% { transform: translateX(-100%) }
}
.notice-btn {
    width: 75px;
    height: 26px;
    line-height: 24px;
    margin-left: 10px;
    font-size: 14px;
    text-align: center;
    border: 1px solid #d3b797;
    border-radius: 13px;
    color: #d3b797;
    cursor: pointer;
}
.notice-btn:hover {
    background-color: #d3b797;
    color: #fff;
}
.notice-icon {
    width: 53px;
    height: 63px;
    margin-right: 8px;
}
.notice-icon img {
    width: 100%;
    height: 100%;
    position: relative;
    top: 5px;
}
</style>
